

import *as mockApi   from './api/api.js'
import { mapState ,mapGetters,mapMutations } from 'vuex'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'




mockApi.getItemList().then(res=>{
console.log(res)
}).catch(err=>{

});
export default {
name: "app",
components:{
Tab,TabItem,XHeader,Actionsheet
},
created() {
var loginStatus=window.localStorage.getItem('loginStatus');

if(loginStatus){
this.mutationIsLogined();
this.mutationLoginname(window.localStorage.getItem('loginname'))
this.mutationsasst(window.localStorage.getItem('asst'))
}
},
methods: {

...mapMutations(['mutationIsLogined','mutationLoginname','mutationsasst']),
routeto(index){
console.log(index)
this.$router.push('/top/'+this.topics[index].name);

},

handleCMore(menukey,menuitem){
this.$router.push('/'+menukey)

},
showMore(){
this.isShow=true
},
},
computed:{
...mapState(['isLogined','unLoginedMenus','isLoginedMenus','topics']),
isShow:{

get(){return this.$store.state.isShow },

set(nval){this.$store.commit('mutationIsshowMore',nval)}
},
...mapGetters(['moreMenus']),








},
data() {
return {













}
},

};



