export default {
    name:'logout',
    created() {
        // 1.修改状态数据
        this.$store.commit('mutationLoginout');
        // 2.清空本地存储数据
        window.localStorage.clear();
        // 3.页面跳转
        this.$router.push('/top/all')

    },
    
}



