
import { mapMutations } from 'vuex'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
name:'login',
components:{
Group,XInput,XButton
},
data() {
return {
accesstoken:'',
}
},
methods: {

...mapMutations(['mutationIsLogined','mutationLoginname','mutationsasst']),
dologin(){
const app=this;
this.$http.post('/accesstoken',{
accesstoken:this.accesstoken
}).then(function(res){
console.log(res.data)

app.mutationIsLogined();
app.mutationLoginname(res.data.loginname);
app.mutationsasst(app.asst);
app.$router.push('/user/'+res.data.loginname);


window.localStorage.setItem('loginStatus',true) 
window.localStorage.setItem('loginname',res.data.loginname);
window.localStorage.setItem('asst',app.asst);


})
}
},

}



