// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue/dist/vue.common.js'
import FastClick from 'fastclick'
import App from './App'
import router from './router'
import Vuex from 'vuex'
import axios from 'axios'
import './mock/index.js'


Vue.prototype.$http=axios.create({
  baseURL:'https://cnodejs.org/api/v1',
});


Vue.use(Vuex);
const store = new Vuex.Store({
    state:{
        asst:'',
        loginname:'',
        isLogined:false,  //登陆状态 true登录 false未登录
        isShow:false,  // 
        unLoginedMenus:{  //未登录
          login:'登录',
          
        },
        isLoginedMenus:{  //登录
          center:'个人中心',
          msg:'消息通知',
          logout:'登出',
        },
        topics:[
            {name:'all',desc:'全部'},
            {name:'good',desc:'精华'},
            {name:'share',desc:'分享'},
            {name:'ask',desc:'问答'},
            {name:'job',desc:'招聘'},
            {name:'test',desc:'测试'}
    
          ]
    },
    getters:{
      moreMenus(state){
        // 参数表示当前store对象的state
        if(state.isLogined){
          return state.isLoginedMenus;
        }
        return state.unLoginedMenus;
      }
    },
    mutations:{ 
        mutationIsshowMore(state,payload){
            state.isShow=payload;
        },
        // 修改登录状态
        mutationIsLogined(state){
          state.isLogined=true
        },
        mutationLoginname(state,payload){
          state.loginname=payload
        },
        mutationsasst(state,payload){
          state.asst=payload
        },
        // 注销
        mutationLoginout(state){
          state.isLogined=false;
          state.loginname='';
          state.asst='';
        }
    },
    actions:{
      
    }

})



FastClick.attach(document.body)
// 阻止vur
Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app-box')



