
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

export default {
props:['name'],
components:{
Cell,Group,Scroller
},
data(){
return {
lists:[],
page:1,  //当前页数
limit:5,  //每页显示
pullupConfig:{  // 上拉
content:'上拉加载更多',
downContent:'松开后进行加载',
upContent:'加载更多',
loadingContent:'加载中...'

},
pulldownConfig:{  // 下拉
content:'下拉加载更多',
downContent:'松开后进行加载',
upContent:'加载更多',
loadingContent:'加载中...'

}
}
},
methods:{
loadMoreData(){
this.page=this.page+1;
var app=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:this.page,
limit:this.limit,
}
}).then(function(res){

for(var i=0;i<res.data.data.length;i++){
app.lists.push(res.data.data[i])
}



app.$refs.Pullup.donePullup();

})
},




loadMoreDatas(){
this.page=this.page+1;
var app=this;
this.$http.get('/topics',{
params:{
tab:this.name,
page:1,
limit:this.limit,
}
}).then(function(res){

app.lists=res.data.data



app.$refs.Pullup.donePulldown();

})
}
},





beforeRouteEnter (to, from, next) {

next(function(vm){
vm.$http.get('/topics',{
params:{
tab:to.params.name,
page:vm.page,
limit:vm.limit,
}
}).then(function(res){
console.log(res)
if(res.data){
vm.lists=res.data.data
}
})
})
},
beforeRouteUpdate(to, from, next) {
var app=this;

this.$http.get('/topics',{
params:{
tab:to.params.name,
page:this.page,
limit:this.limit,
}
}).then(function(res){
console.log(res)
app.lists=res.data.data

})

next();
},
}



